import Vehicle from '../models/Vehicle'

export const createVehicle = async(req, res) => {
    const { brand, cod_tdp, serie_tdp, category, model, colour, manufacturing_year, model_year, branch_office, location, price, imgURL, status } = req.body;

    const newVehicle = new Vehicle({
        brand,
        cod_tdp,
        serie_tdp,
        category,
        model,
        colour,
        manufacturing_year,
        model_year,
        branch_office,
        location,
        price,
        imgURL,
        status
    });

    const vehicleSaved = await newVehicle.save();

    res.status(201).json(vehicleSaved);
}

export const getVehicles = async(req, res) => {
    const vehicles = await Vehicle.find();
    res.json(vehicles);
}

export const getVehicleById = async(req, res) => {
    const { vehicleId } = req.params;
    const vehicle = await Vehicle.findById(vehicleId);
    res.status(200).json(vehicle);

}

export const getVehicleBySerie = async(req, res) => {
    try {
        const { serie } = req.body;
        const carro = await Vehicle.findOne({ serie_tdp: serie });
        //console.log(carro);
        if (carro) {
            console.log(carro);
            res.status(200).json(carro);
        } else {
            res.status(201).json({ messsage: 'No existe vehÃ­culo a mostrar' });
        }
    } catch (e) {
        console.log(e);
        res.status(401).json({ messsage: 'No se puede ejecutar la consulta' });
    }
}

export const updateVehicleById = async(req, res) => {
    const { vehicleId } = req.params;
    const updatedVehicle = await Vehicle.findByIdAndUpdate(vehicleId, req.body, { new: true });
    res.status(200).json(updatedVehicle);
}

export const deleteVehicleById = async(req, res) => {
    const { vehicleId } = req.params;
    const deletedVehicle = await Vehicle.findByIdAndRemove(vehicleId);
    res.status(201).json({ message: 'Vehicle Removed' });
}